import {Inject, NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';

import {NgxUaaLibModule} from 'ngx-uaa';

import {CommonModule} from '@angular/common';
import {TranslateService} from '@ngx-translate/core';
import {NgxJsonFormModule} from 'ngx-json-form';
import {ACCOUNT_ROUTES} from './account.routing';
import {ClrModalModule} from '@clr/angular';
import {MyDataModalComponent} from './component/my-data-modal/my-data-modal.component';

@NgModule({
  declarations: [
    MyDataModalComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(ACCOUNT_ROUTES),

    NgxJsonFormModule,
    NgxUaaLibModule,

    ClrModalModule,
  ],
  exports: [RouterModule, MyDataModalComponent],
  providers: []
})
export class AccountModule {
  constructor(translate: TranslateService, @Inject('i18n_DEFAULT_LANG') private i18nDefaultLang: string) {
    translate.use(i18nDefaultLang);
  }
}
